.owl-item.active {
	p.active {
		// color: $orange;
		animation: 0.5s change 0.5s ease both;
	}
}
@keyframes change {
	from {
		color: white;
	}

	to {
		color: $orange;
	}
}

.owl_jump {
	.owl-prev {
		float: left;

		&:before {
			color: $navy!important;
		}
	}

	.owl-next {
		&:before {
			color: $navy!important;
		}
	}
}

.owl-carousel {
	position: relative;

	.item {
		position: relative;
		height  : 100%;
		width   : 100%;

		img {
			position: relative;
		}
		// p {
		// 	position : absolute;
		// 	font-size: 1.8rem;
		// 	top      : 50%;
		// 	left     : 50%;
		// 	transform: translate(-50%,-50%);
		// }
	}

	.owl-controls {
		position : relative;
		top      : 0;
		left     : 0;
		transform: translate(0%,-100%);
		width    : 100%;
	}

	.owl-nav {
		position : absolute;
		top      : 50%;
		left     : 22%;
		transform: translate(-22%, -50%);
		width    : 60%;

		.owl-prev {
			float: left;

			&:before {
				font-family: "ETmodules" !important;
				content    : "\34";
				font-size  : 5em;
				color      : white;
			}
		}

		.owl-next {
			float: right;

			&:before {
				font-family: "ETmodules" !important;
				content    : "\35";
				font-size  : 5em;
				color      : white;
			}
		}
	}

	.owl-dots {
		position : absolute;
		bottom   : 0;
		left     : 50%;
		transform: translateX(-50%);
	}
}

.owl-dots .owl-dot {
	display: inline-block;
	zoom   : 1;

	span {
		width                      : 10px;
		height                     : 10px;
		margin                     : 5px 7px;
		background                 : $lightgrey;
		display                    : block;
		-webkit-backface-visibility: visible;
		transition                 : opacity 0.2s ease;
		border-radius              : 30px;
	}

	&.active {
		span {
			background: $orange;
		}
	}
}

.owl-carousel {
	.owl-stage {
		display: flex;
	}

	.owl-item {
		display: flex;
		flex   : 1 0 auto;
	}

	.caption {
		display       : flex;
		flex          : 1 0 auto;
		flex-direction: column;
	}

	.flex-text {
		flex-grow: 1;
	}

	.thumbnail {
		display       : flex;
		flex-direction: column;
		margin        : 0 15px;
	}
}