.popup {
	display         : none;
	/*hidden on pageload*/
	width           : 100vw!important;
	height          : 100%;
	position        : absolute;
	padding-right   : 20%;
	left            : 0;
	z-index         : 9;
	top             : 0;
	background-color: #f2f2f2;

	.et_pb_code {
		position  : absolute;
		right     : -50%;
		text-align: center;
		top       : -10%;
		width     : 100%;

		.close {
			// position: absolute;
			// left: -10%;
			font-size  : 3em;
			font-weight: 100;
			color      : grey;
		}
	}

	.new_wrap {
		padding: 5% 0;
		height : 100%;
		display: block;
		width  : 80%;
	}
}

.tile_hover {
	display  : flex;
	flex-wrap: wrap;

	&.our_team {
		.et_pb_column {
			display        : flex;
			flex-wrap      : wrap;
			justify-content: center;

			.no_row {
				width  : 25%!important;
				float  : left;
				padding: 1%!important !important;
				margin : 0;
			}
		}
	}

	.mobile_hide {
		@include bp(phone) {
			display: none;
		}
	}
	@include bp(phone) {
		padding: 0;
		// width:90%;
		.et_pb_row_13 {
			.et_pb_column {
				@include bp(phone) {
					.et_pb_blurb {
						&:nth-child(1) {
							display: none;
						}
					}
				}
			}
		}
	}

	.et_pb_column {
		// display: flex;
		// flex-wrap:wrap;
		// justify-content: center;
		// .no_row{
		// 	width: 30%!important;
		// 	float: left;
		// 	padding: 1%!important;
		// }
		@include bp(phone) {
			display       : flex;
			flex-direction: row;
			margin        : 0 !important;
		}

		.et_pb_blurb {
			width     : 100%;
			// height: 100%;
			padding   : 3%;
			// overflow: hidden;
			text-align: center;
			@include bp(phone) {
				padding: 0 3% 3% 0;
			}

			.et_pb_blurb_content {
				display: table;
				margin : 0;
			}

			.et_pb_blurb_container {
				width   : 100%;
				height  : 100%;
				position: absolute;
				// overflow: hidden;
				top     : 0;
				left    : 0;
				padding : 10% 0;
			}

			.et_pb_main_blurb_image {
				display   : table;
				background: $orange;
				// overflow: hidden;
				margin    : 0 auto;

				img {
					display: table-cell;
					// max-width: none;
					opacity: 1;
					// width: calc(100% + 20px);
					// transition: opacity 0.35s, transform 0.35s;
					// transform: translate3d(-20px, 0, 0);
				}
			}

			p {
				margin: 5%;
			}

			.name {
				color         : white;
				@extend .heading6;
				font-family   : $gotham;
				font-weight   : 600;
				line-height   : 1.5em;
				text-align    : right;
				letter-spacing: 0.05em;
				opacity       : 0;
				position      : absolute;
				bottom        : 5%;
				right         : 5%;
				filter: alpha(opacity=0);
				transition    : opacity 0.35s, transform 0.35s;
				transform     : translate3d(100%, 0, 0);
			}

			.social {
				width     : 10%;
				opacity   : 0;
				position  : absolute;
				top       : 5%;
				left      : 5%;
				filter: alpha(opacity=0);
				transition: opacity 0.35s, transform 0.35s;
				transform : translate3d(100%, 0, 0);
			}
		}

		.current,
		.et_pb_blurb:hover {
			.name {
				opacity  : 1;
				filter: alpha(opacity=100);
				transform: translate3d(0, 0, 0);
			}

			.social {
				opacity  : 1;
				filter: alpha(opacity=100);
				transform: translate3d(0, 0, 0);
			}

			img {
				opacity  : 0.4!important;
				filter: alpha(opacity=40);
				transform: translate3d(0, 0, 0);
			}
		}
	}

	.et_pb_column {
		.tile_click {}
	}
}